
.total {
  width: 75%;
  margin: 50px auto;
}
.total td {
    font-size: 1.2em;
    text-align: left;
    padding: 10px;
    margin: 0;
    border-bottom: 1px solid #e5e5e7;
}
.total td.controls {
      text-align: right;
}
.date {
  font-size: .8em;
  color: #666666;
  display: block;
}
.category {
  display: inline-block;
  text-transform: uppercase;
  letter-spacing: 2px;
}
.category-sum {
    display: block;
    font-size: 2em;
    letter-spacing: normal;
    text-transform: none;
    padding: 0 25px;
}
.btn {
  background: transparent;
  font-size: 14px;
  letter-spacing: 1px;
  text-transform: uppercase;
  cursor: pointer;
  opacity: .5;
  transition: opacity .3s ease;
}
.btn:hover {
    opacity: 1;
}
.btn-add {
    color: #956C1F;
    margin-bottom: 20px;
    padding: 15px 30px;
    border: 2px solid #956C1F;
}
.btn-delete {
    width: 30px;
    height: 30px;
    position: relative;
    border: 1px solid #180F01;
    border-radius: 50%;
}
.btn-delete:before, .btn-delete:after {
      content: '';
      width: 1px;
      height: 15px;
      display: inline-block;
      position: absolute;
      top: 50%;
      left: 50%;
      background: #180F01;
      -webkit-backface-visibility: hidden;
              backface-visibility: hidden;
}
.btn-delete:before {
      -webkit-transform: translate(-50%, -50%) rotate(45deg);
              transform: translate(-50%, -50%) rotate(45deg);
}
.btn-delete:after {
      -webkit-transform: translate(-50%, -50%) rotate(-45deg);
              transform: translate(-50%, -50%) rotate(-45deg);
}
.btn-save {
    color: #956C1F;
    padding: 15px 30px;
    border: 2px solid #956C1F;
}
.fade-enter-active, .fade-leave-active {
  transition: opacity .5s;
}
.fade-leave-active {
  opacity: 0;
}



