
body {
  background: #FFFBF5;
  font-family: 'Ubuntu', Helvetica, Arial, sans-serif;
  font-size: 14px;
  color: #180F01;
  -webkit-font-smoothing: antialiased;
  -moz-osx-font-smoothing: grayscale;
  text-align: center;
  margin-top: 60px;
}
body h1 {
    font-size: 3em;
    text-align: center;
}
body nav {
    margin: 50px 0;
}
body .nav-item {
    display: inline-block;
    font-size: 1.2em;
    color: #180F01;
    text-decoration: none;
    padding: 3px;
    margin: 7px;
}
body .nav-item.router-link-exact-active {
      border-bottom: 3px solid #180F01;
}
body .input {
    background: transparent;
    padding: 15px;
    margin-right: 10px;
    border: none;
    border-bottom: 1px solid #180F01;
}
body .input-number {
      -moz-appearance: textfield;
      -webkit-appearance: none;
}
body .custom-select {
    background: transparent url("assets/arrow.svg") center right no-repeat;
    padding: 15px 30px 15px 15px;
    border: 1px solid #180F01;
    -webkit-appearance: none;
    -moz-appearance: none;
    cursor: pointer;
}



